import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IPassbook } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { PsCurrencyPipe } from 'src/app/commonSRC/psPipes/ps-currency/ps-currency.pipe';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IMapKeyValue, IOptionsPsLabelInput } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexLookUpComponentExposed } from '../../ps-complex-components/ps-complex-lookup/ps-complex-lookup.component.interfaces';
import { IOptionsPsLookupPassbooksExposed } from './ps-lookup-passbooks.component.interfaces';



/**
 * @author Zunair.Zakir
 * @since 09/06/2021
 *
 * <p> PsComplexOwnAccountComponent is complex component to show passcodes list fetched from server, balance and available balance</p>
 */
@Component({
  selector: 'ps-lookup-passbooks',
  templateUrl: './ps-lookup-passbooks.component.html',
  styleUrls: ['./ps-lookup-passbooks.component.scss'],
})
export class PsLookupPassbooksComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsLookupPassbooksExposed = {
    requestObject: null,
  };
  public complexLookupOptions: IOptionsPsComplexLookUpComponentExposed = {
    requestObject: null,
    notFoundMessage: 'no_valid_passbook_found_key',
    multiple: this.options.multiple ? this.options.multiple : false
  };
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() public onPassbookChange: EventEmitter<any> = new EventEmitter<any>();
  public listOfPassbook?: IPassbook[];
  public labelValuesMap = new Map<string, IMapKeyValue>();
  private psCurrency = new PsCurrencyPipe();
  public labelOptions: IOptionsPsLabelInput = {};
  private allType = false;

  constructor(
    public commonService: PsCommonService,
    logger: LoggerService,
    private omniPull?: OmniPullService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    super.init();
    this.returnPassbookList();
    this.complexLookupOptions.labelKey = this.options.labelKey;
    this.complexLookupOptions.title = 'title_key';
    this.complexLookupOptions.subTitle = 'sub_title_key';
    this.complexLookupOptions.labelsValueMap = this.labelValuesMap;
    this.allType = this.options.allType ? this.options.allType : false;
    this.commonProv.copyObject(this.complexLookupOptions, this.options, false);
    this.complexLookupOptions.requestObject = this.options.requestObject;
  }

  /**
   * fetching passbook list from server
   */
  private async returnPassbookList() {
    this.complexLookupOptions.listOfOptions = undefined;
    const params = {};
    const result = await this.omniPull.returnPassbooks(params).catch(error => {
      this.logger.error('Error: While fetching PassbookList :', error);
    });

    if (result && result.gridModel.length > 0) {
      this.options.listOfPassbook = result.gridModel;
      this.complexLookupOptions.listOfOptions = this.populateCustomPassbooks(result.gridModel);
    } else {
      this.complexLookupOptions.listOfOptions = [];
    }
  }

  /**
   *
   * @param listOfOptions
   */
  private populateCustomPassbooks(listOfOptions: any[]): any[] {

    const passbooksList: IPassbook[] = [];
    for (const iterator of listOfOptions) {
      const passcode: IPassbook = {
        accGl: iterator.account.accGl,
        branch: iterator.account.branch,
        currency: iterator.account.currency,
        serialNo: iterator.account.serialNo,
        cif: iterator.account.cif,
        additionalRef: iterator.account.additionalRef,
        lookupKey: iterator.account.accGl,
        passbookCode: iterator.passbookCode,
        previewProperty: 'accGl'
      };
      passbooksList.push(passcode);
    }
    return passbooksList;
  }

  onChangeItem(event) {
    this.onPassbookChange.emit(event);
  }

}
